.App {
  height: 100%;
}

#banner-image {
  width: 100%;
  background-image: url('../images/banner.jpg');
  background-size: cover;
  height: 300px;
}

.menu-nav {
  width: 100%;
  background-color: #c6d4e1;
  text-transform: uppercase;
  font-size: .8em;
}

#navigation {
  display: flex;
  margin: 0;
  padding: 12px 6px 12px 6px;
}

#navigation li {
  padding: 0;
  color: #545454;
}

#navigation li:not(:first-child):before {
  content: '|';
  color: #a4bacf;
  margin-right: 20px;
}

#navigation li a {
  margin-right: 20px;
  padding: 7.5px 10px;
  text-decoration: none;
  text-transform: inherit;
}

.menu-active {
  background: rgba(25, 25, 25, 0.2);
  color: #fff;
}

article {
  padding: 20px;
}